<template>
  <div class="container">
    <div class="content">
      <div class="container_inside">
        <div class="title">
           
        </div>
        <div class="sub_title">
               .
        </div>
        <div class="user_info">
          <div class="user_info_inside">
            <table id="table" class="table">
              <colgroup>
                <col style="width:20%">
                <col style="width:70%">
                <col style="width:10%">
              </colgroup>
              <tr>
                <th class="line" @click="passwordChange()"> </th>
                <td class="line2" @click="passwordChange()">   </td>
                <td class="line" @click="passwordChange()"><img class="table_img" src="@/assets/right.png"/></td>
              </tr>
              <tr>
                <th class="line" @click="nameChange()"> </th>
                <td class="line" @click="nameChange()"> {{User.name}}</td>
                <td class="line" @click="nameChange()"><img class="table_img" src="@/assets/right.png"/></td>
              </tr>
              <tr>
                <th class="line" @click="birthChange()"> </th>
                <td class="line" @click="birthChange()">  {{User.birth}}</td>
                <td class="line" @click="birthChange()"><img class="table_img" src="@/assets/right.png"/></td>
              </tr>
              <tr>
                <th class="line" @click="sexChange()"> </th>
                <td class="line" @click="sexChange()">  {{User.sex}}</td>
                <td class="line" @click="sexChange()"><img class="table_img" src="@/assets/right.png"/></td>
              </tr>
              <tr>
                <th @click="challengeChange()"> </th>
                <td class="line3" @click="challengeChange()">  </td>
                <td @click="challengeChange()"><img class="table_img" src="@/assets/right.png"/></td>
              </tr>
            </table>
          </div>
        </div>
        <div class="contact_info">
          <div class="container_inside">
            <div class="user_info_inside">
              <table id="contact_table" class="table">
                <colgroup>
                  <col style="width:20%">
                  <col style="width:70%">
                  <col style="width:10%">
                </colgroup>
                <tr>
                  <th class="line" @click="mailChange()"> </th>
                  <td class="line" @click="mailChange()">  {{User.mail}}</td>
                  <td class="line" @click="mailChange()"><img class="table_img" src="@/assets/right.png"/></td>
                </tr>
                <tr>
                  <th @click="phoneChange()"> </th>
                  <td @click="phoneChange()"> {{User.phone}} </td>
                  <td @click="phoneChange()"><img class="table_img" src="@/assets/right.png"/></td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import $ from "jquery";
import {mapState} from 'vuex'


export default {
  name: "userInfo",
  computed:{
    ...mapState(["userInfo"])
  },
  data() {
    return {
      User: [],
      challenge: ''
    }
  },
  async created() {
    this.User = this.$store.state.userInfo
    this.challenge = Object.keys(this.User.challenge).toString()
  },
  mounted() {
    $(document).ready(function(){
      changeColor();
      clickTd();
      clickTr();
    })
    function changeColor(){
      $('#table tr').mouseover(function(){
        $(this).addClass('changeColor');
      }).mouseout(function() {
        $(this).removeClass('changeColor');
      });
      $('#contact_table tr').mouseover(function(){
        $(this).addClass('changeColor');
      }).mouseout(function() {
        $(this).removeClass('changeColor');
      });
    }
    function clickTd(){
      $("#table tr td").click(function(){
        // var text = $(this).text();
        // alert(text);
      });
    }
    function clickTr(){
      $("#table tr").click(function(){
        this.tr = $(this);
        this.td = this.tr.children();
        this.idx = this.td.eq(0).text()
        this.cont = this.td.eq(1).text()
      });
    }
  },
  methods: {
    nameChange: function() {
      this.$router.push({name: 'Name'})
    },
    birthChange: function() {
      this.$router.push({name: 'Birth'})
    },
    sexChange: function() {
      this.$router.push({name: 'Sex'})
    },
    mailChange: function() {
      this.$router.push({name: 'Email'})
    },
    phoneChange: function() {
      this.$router.push({name: 'Phone'})
    },
    passwordChange: function () {
      this.$router.push({name: 'Password'})
    },
    challengeChange: function() {
      this.$router.push({name: 'Challenge'})
    }
  }
}


</script>

<style scoped>

@import url('https://fonts.googleapis.com/css2?family=Gothic+A1:wght@700&display=swap');

.changeColor {
  background-color: #eeeeee;
}

.container {
  font-size: 1em;
}

.content {
  position: relative;
  background-color: white;
  width: 50%;
  height: 1200px;
  margin-left: 25%;
}

.container_inside {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.title {
  margin-top: 10px;
  text-align: left;
  font-size: 1em;
  font-family: 'Gothic A1', sans-serif;
}

.sub_title {
  margin-top: 10px;
  text-align: left;
  font-size: 0.6em;
  font-family: 'Gothic A1', sans-serif;
}

.user_info {
  position: relative;
  margin-top: 20px;
  width: 100%;
  /*height: 35%;*/
  border-radius: 16px;
  border: 1px solid lightgray;
  padding-top: 40%;
}

.user_info_inside {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.table {
  width: 100%;
  height: 100%;
}

.table th {
  color: black;
  font-size: 0.7em;
}

.table tr {

}

.table_img {
  max-width: 15%;
  color: #aaaaaa;
}

.line {
  border-bottom: 1px solid lightgray;
}

.line2 {
  font-size: 0.8em;
  color: dimgray;
  border-bottom: 1px solid lightgray;
}

.line3 {
  font-size: 0.8em;
  color: dimgray;
}

.contact_info {
  position: relative;
  margin-top: 10px;
  width: 100%;
  padding-top: 25%;
  border-radius: 16px;
  border: 1px solid #d3d3d3;
}

@media all and (min-width: 768px) and (max-width: 1023px) {
  .container {
    font-size: 0.8em;
  }

  .content {
    width: 80%;
    margin-left: 10%;
  }
}

@media (max-width: 767px) {
  .container {
    font-size: 0.7em;
  }

  .content {
    width: 80%;
    margin-left: 4%;
  }
}

</style>